/* 在CSS文件中引入antd的css文件 */
@import '~antd/dist/antd.css';

*{
  margin:0;
  padding:0;
}
.header{
  height:50px;
  background-color: #444;

}
body{
  background-color: #ccc;

}
.header header{
  width:100%;
  line-height:50px;
  margin:0 auto;
}
.header header a{
  margin: 0 20px;
  color: #fff;
}
.header img{
  width:120px;
}

.content{
  width:100%;
  margin:30px auto 0;
}
.left{
  height:900px;
  width:900px;
  float:left;
}
.right{
  width:270px;
  height:900px;
  float:right;
  background-color:#ccc;
}


